<template>
    <div class="vipDescription_container">
        <head-top head-title="我的优惠" goBack="true"></head-top>
        <section class="vipDescription_body">
            <!-- <blockquote> 与 </blockquote> 
            之间的所有文本都会从常规文本中分离出来，经常会在左、右两边进行缩进（增加外边距），而且有时会使用斜体。也就是说，块引用拥有它们自己的空间。 -->
             <blockquote>
                <p>尊敬的用户，随着会员体系逐渐完善，自2016年10月10日起，饿了么会员权益将做如下优化：
                购卡后31天内，累积享有30单减免配送费服务（每日最多3单，每单最高减免4元）。
                注：已购买的会员服务不受影响，当前会员服务失效前无法购买新卡。</p>
            </blockquote>
            <h3 id="q1-">Q1: 特权介绍</h3>
            <ul>
                <li>身份标识：饿了么会员服务有效期内，享有专属皇冠标识。</li>
                <li>减免配送费： 饿了么会员卡自绑定账户之日起31天内，在「蜂鸟专送」标识商家下单，享有30次减免配送费特权，每日最多减免3单，每单最高可减4元。</li>
                <li>更多特权，敬请期待！</li>
            </ul>
            <h3 id="q2-">Q2: 资费介绍</h3>
            <ul>
                <li>饿了么会员卡：20元</li>
            </ul>
            <h3 id="q3-">Q3: 使用说明</h3>
            <p>当用户满足以下任一条件，会员服务自动失效：</p>
            <ol>
                <li>自绑定之日起超过31天；</li>
                <li>在31天内累计使用减免配送费的蜂鸟订单数量达到30单；</li>
            </ol>
            <h3 id="q4-">Q4: 购卡说明</h3>
            <ul>
                <li>在线购买：饿了么App&gt;我的&gt;饿了么会员卡</li>
            </ul>
            <h3 id="q5-">Q5: 温馨提示</h3>
            <ul>
                <li>用户在当前会员服务失效前，无法购买新卡。</li>
                <li>请认准饿了么官方渠道，任何从其他第三方途径获得的会员卡，饿了么不保证其可用性。</li>
            </ul>
        </section>
    </div>
</template>

<script>
import headTop from '../../../components/header/header'
export default {
    components: {
        headTop
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.vipDescription_container{
    position: fixed;
    left:0;
    right:0;
    top:0;
    bottom: 0;
    background: #fff;
    z-index:105;
    padding-top: 1.95rem;
    overflow-y: auto;
}
.vipDescription_body{
    padding:0.6rem;
    blockquote{
        @include sc(0.6rem,#333);
    }
    h3{
         line-height: 1rem;
        @include sc(0.7rem,#333);
        font-weight: bold;
    }
    ul{
        line-height: 1rem;
        li{
            @include sc(0.6rem,#333);
        }
    }
    ol{
         line-height: 1rem;
        li{
             @include sc(0.6rem,#333);
        }
    }
    p{
         line-height: 1rem;
         @include sc(0.6rem,#333);
    }
}
</style>



